.arrow-btn {
  border-radius: 6px;
  background-clip: padding-box;
  font-size: 1.125em;
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  padding: 10px 25px;
  border: 1px solid #2638C4;
  background-color: #fff;
  transition: all 0.3s ease 0s;
  white-space: nowrap;
  cursor: pointer;
}
.arrow-btn a {
  cursor: pointer;
}
.arrow-btn svg {
  position: relative;
  transition: right 0.2s ease 0s;
  right: 4px;
}
.arrow-btn:hover {
  box-shadow: -1px 6px 10px 0 rgba(120, 60, 20, 0.2);
  color: #fff;
}
.arrow-btn:hover svg {
  right: 0px;
  position: relative;
}
.arrow-btn svg {
  width: 20px;
  margin-left: 5px;
}
.arrow-btn.white-arrow-btn {
  background-color: #fff;
  color: #2638C4;
}
.arrow-btn.purple-arrow-btn {
  background-color: #2638C4;
  color: #fff;
  border: none;
}
.arrow-btn.purple-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.blue-arrow-btn {
  background-color: #006cb4;
  color: #fff;
  border: none;
}
.arrow-btn.blue-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.green-arrow-btn {
  background-color: #33A99D;
  color: #fff;
  border: none;
}
.arrow-btn.green-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.fb-arrow-btn {
  background-color: #fff;
  color: #006cb4;
  border: none;
  transition: all 0.3s ease 0s;
}
.arrow-btn.fb-arrow-btn svg {
  transition: none !important;
  right: 0px;
}
.arrow-btn.fb-arrow-btn svg path {
  fill: #fff;
}
.arrow-btn.fb-arrow-btn svg:hover {
  box-shadow: -1px 6px 10px 0 rgba(120, 60, 20, 0.2);
  color: #fff;
}
.arrow-btn.fb-arrow-btn svg:hover svg {
  right: 4px !;
  position: relative;
}
.arrow-btn.transparent-arrow-btn {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
}
.arrow-btn.transparent-arrow-btn svg path {
  fill: #fff;
}
.pc-container a {
  color: #005690;
}
.producer-hero {
  /*! -- Stack Sliders -- */
}
.producer-hero .flickity-prev-next-button {
  transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
  -moz-transition: 0.3s ease;
  border-radius: 0;
  background: #252525;
  width: 2.78571429em;
  height: 5.57142857em;
}
.producer-hero .flickity-page-dots {
  bottom: -35px !important;
}
.producer-hero .flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
  fill: #fff;
}
.producer-hero .flickity-prev-next-button:not(:hover) {
  opacity: 0.5;
}
.producer-hero .flickity-prev-next-button:hover {
  background: #252525;
}
.producer-hero .flickity-prev-next-button.previous {
  left: 0;
}
.producer-hero .flickity-prev-next-button.next {
  right: 0;
}
.producer-hero .flickity-prev-next-button:before {
  color: #fff;
}
.producer-hero .slider.slider--inline-arrows .flickity-prev-next-button {
  background: none;
}
.producer-hero .slider.slider--inline-arrows .flickity-prev-next-button:before {
  font-size: 1.78571429em;
  color: #252525;
  content: '\e802';
}
.producer-hero .slider.slider--inline-arrows .flickity-prev-next-button.previous:before {
  content: '\e801';
}
.producer-hero .slider.slider--columns .previous {
  left: 15px;
}
.producer-hero .slider.slider--columns .next {
  right: 15px;
}
.producer-hero .slider.border--round .slides li {
  border-radius: 6px;
  overflow: hidden;
}
.producer-hero .slider.boxed--border .slides li {
  border: 1px solid #ececec;
}
.producer-hero .slider .slides li .feature:not(.feature-9) {
  margin-bottom: 0;
}
.producer-hero .slide .boxed:last-child {
  margin-bottom: 0;
}
.producer-hero .slide:not([class*='col-']) > img:only-child {
  width: 100%;
}
.producer-hero .bg--dark .slider.slider--inline-arrows .flickity-prev-next-button:before,
.producer-hero .bg--primary .slider.slider--inline-arrows .flickity-prev-next-button:before,
.producer-hero .imagebg:not(.image--light) .slider.slider--inline-arrows .flickity-prev-next-button:before {
  color: #fff;
}
@media all and (max-width: 990px) {
  .producer-hero .flickity-prev-next-button {
    display: none;
  }
}
@media all and (min-width: 768px) {
  .producer-hero h2 + .slider,
  .producer-hero .h2 + .slider {
    margin-top: 3.71428571em;
  }
}
.producer-hero .slider.slider--ken-burns:not(.parallax) .slide.is-selected > .background-image-holder,
.producer-hero .slider.slider--ken-burns:not(.parallax) .slide.is-selected > img:only-child {
  animation: kenBurns 15s ease alternate infinite;
  -webkit-animation: kenBurns 15s ease alternate infinite;
}
.producer-hero .radial {
  margin-left: auto;
  margin-right: auto;
}
.producer-hero .radial .radial__label {
  transition: opacity 0.3s ease;
  -webkit-transition: opacity 0.3s ease;
}
.producer-hero .radial:not(.radial--active) .radial__label {
  opacity: 0;
}
.producer-hero .radial:not(:last-child) {
  margin-bottom: 1.85714286em;
}
@media all and (max-width: 767px) {
  .producer-hero .radial {
    margin-bottom: 1.85714286em;
  }
}
.producer-hero .carousel {
  background: #222;
  box-shadow: 0 23px 40px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
  width: 100%;
}
.producer-hero .carousel-cell {
  width: 100%;
  margin-right: 10px;
  /* center images in cells with flexbox */
  display: flex;
  align-items: center;
  justify-content: center;
}
.producer-hero .carousel.is-fullscreen .carousel-cell {
  height: 100%;
}
.producer-hero .carousel-cell-image {
  display: block;
  max-height: 100%;
}
.producer-hero .carousel.is-fullscreen .carousel-cell-image {
  max-width: 100%;
}
.producer-hero__text-img-cta {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  padding: 60px 20px;
  border-bottom: 1px solid #E9E9E9;
}
@media (max-width: 768px) {
  .producer-hero__text-img-cta {
    display: flex;
    flex-direction: column;
    padding: 30px 20px;
    gap: 60px;
    flex-direction: column-reverse;
  }
}
.producer-hero__text-img-cta img {
  box-shadow: 0 23px 40px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
  width: 100%;
}
@media (max-width: 425px) {
  .producer-hero__text-img-cta .arrow-btn {
    white-space: normal;
    text-align: center;
  }
}
.producer-hero__contact {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  padding: 30px 20px;
}
.producer-hero__contact p {
  margin: 0;
  padding: 0;
  max-width: 300px;
}
.producer-hero__contact ul {
  margin: 0;
  padding: 0;
}
@media (max-width: 768px) {
  .producer-hero__contact {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }
}
